.form-toggle-link {
  cursor: pointer;
  color: white;
  font-weight: 500;
  font-size: 15px;
  line-height: 48px;
  white-space: nowrap;
  display: flex;
  max-width: 100%;
  align-items: center;
}

.form-toggle-link:hover {
  text-decoration: underline;
}

.form-toggle-link:focus {
  outline: none;
  text-decoration: underline;
}

.form-toggle-link__text {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
}

.form-toggle-link__arrow {
  position: relative;
  top: 2px;
  transition: transform 250ms;
  cursor: pointer;
  display: inline-block;
  fill: white;
}

.form-toggle-link__arrow_up {
  transform: rotate(180deg);
}